@import (reference) "~@less/shared/fonts.less";
  
.vjs-menu-button-popup .vjs-menu {
  width: 13em;
  // min-width: 5em;
  left: 50%;
  transform: translateX(-50%);

  .vjs-menu-content {
    bottom: 1em;
    font-family: @font-family-sans-serif;
    font-size: @fs-xsmall;
    max-height: 22em;
    background: @black;
  }

  .vjs-menu-title {
    text-align: left;
    padding: 0.5em;
  }

  .vjs-menu-item {
    text-transform: uppercase;
    text-align: left;
    padding: 0.5em;
    display: flex;
    align-items: center;
    justify-content: space-between;

    &.vjs-selected, &:hover {
      background: none;
      color: var(--accent-color);
    }

    &.vjs-selected {
      &::after {
        font-family: 'balmoral-icons';
        content: "\e954";
        margin-left: 5px;
      }
    }
  }
}