@import (reference) "~@less/shared/fonts.less";

.vjs-time-control {
  padding-left: 5 / @em;
  padding-right: 5 / @em;
  width: auto;
  min-width: 2.5em;

  @media (min-width: @screen-xs-min) {
    padding-left: 10 / @em;
    padding-right: 10 / @em;
    min-width: 4em;
  }
}

.vjs-current-time, .vjs-time-divider, .vjs-duration {
  display: block;
  font-size: 0.65em;
  @media (min-width: @screen-xs-min) {
    font-size: @fs-xsmall;
  }
}

.vjs-current-time {
  padding-right: 2 / @em;
  .vjs-current-time-display {
    display: block;
    text-align: right;
  }
}

.vjs-duration {
  padding-left: 2 / @em;
  .vjs-duration-display {
    display: block;
    text-align: left;
  }
}

.vjs-time-divider {
  padding: 0;
  min-width: 0;
}

.vjs-remaining-time {
  display: none;
}