@import (reference) "~@less/shared/utilities.less";
@import (reference) "~@less/theme/vars.less";

button {
  font-family: inherit;
}

.vjs-button {
  min-height: @small-btn-height / @em;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0.6;
  .lh-transition(opacity .2s linear);

  .vjs-button {
    opacity: 1;
    transition: none;
  }

  &:hover {
    opacity: 1;
    .lh-transition(opacity .2s linear);
  }

  > .vjs-icon-placeholder {
    &::before {
      line-height: 1;
      font-family: 'balmoral-icons';
      position: relative;
      font-size: 1rem;
      display: block;
      @media (min-width: @screen-sm-min) {
        font-size: 1.3125rem;
      }
    }
  }
}

.vjs-control:not(.vjs-play-control):not(.vjs-progress-control):not(.vjs-time-control):not(.vjs-close-button):not(.vjs-volume-panel):not(.vjs-volume-control) {
  width: 2em;

  @media (min-width: @screen-xxs-min) {
    margin: 0 (10 / @em);
  }

  @media (min-width: @screen-sm-min) {
    width: 3em;
  }

  &:focus {
    text-shadow: none;
  }
}