.sepia(@value) {
     -webkit-filter: sepia(@value);
     -moz-filter: sepia(@value);
     filter: sepia(@value);
}

/*Convert units*/
.pxToRem(@sizeValue,@baseFont,@prop){
	@remCalc: (@sizeValue * 1) / @baseFont;
  	@remValueProp: ~"@{remCalc}rem"; 
	@{prop}: @remValueProp;
}

.pxToRemVar(@sizeValue,@baseFont){
	@remCalc: (@sizeValue * 1) / @baseFont;
	@remValue: ~"@{remCalc}rem"; 
}

.pxToVw(@sizeValue,@resolution){
	@vwCalc: (100*@sizeValue)/@resolution;
  	@vwValue: ~"@{vwCalc}vw"; 
}

.for(@i, @n) {.-each(@i)}
.for(@n)     when (isnumber(@n)) {.for(1, @n)}
.for(@i, @n) when not (@i = @n)  {
    .for((@i + (@n - @i) / abs(@n - @i)), @n);
}

.for(@array)   when (default()) {.for-impl_(length(@array))}
.for-impl_(@i) when (@i > 1)    {.for-impl_((@i - 1))}
.for-impl_(@i) when (@i > 0)    {.-each(extract(@array, @i))} 

// Variable width font sizes
.variableFontSize() {
	font-size: 4vw;
	@media (min-width: @screen-sm-min) { font-size: 3vw; }
	@media (min-width: @screen-md-min) { font-size: 2.3vw; }
	@media (min-width: @screen-lg-min) { font-size: 1.8vw; }
	@media (min-width: @screen-xl-min) { font-size: 1.5em; }
}

.variableH1Size() {
	font-size: 50px;
	@media (min-width: @screen-sm-min) { font-size: 65px; }
	@media (min-width: @screen-md-min) { font-size: 75px; }
	@media (min-width: @screen-lg-min) { font-size: 105px; }
}

// Column count prefixing
.columnCount(@count, @gap) {
	column-count: @count;
	-webkit-column-count: @count;
	-moz-column-count: @count;
	column-gap: @gap;
	-webkit-column-gap: @gap;
	-moz-column-gap: @gap;
}

// Multiline ellipsis
// Not perfect: relies on consumer to "know" and match its own line-height & max-height.
.multilineEllipsis(@maxLines) {
	display: block;
	display: -webkit-box;
	-webkit-line-clamp: @maxLines;
	-webkit-box-orient: vertical;
	overflow: hidden;
	text-overflow: ellipsis;
	margin-bottom: 0;

	// Mozilla hack: this works ONLY if the text definitely overflows. If it doesn't, there will be bugs.
	@-moz-document url-prefix() {
		overflow: hidden;
		position: relative;
		&:before {
			background: @blanc;
			bottom: 0;
			position: absolute;
			right: 0;
			float: right;
			content: '\2026';
			margin-left: -2rem;
			width: 2rem;
		}
		&:after {
			content: '';
			background: @blanc;
			position: absolute;
			height: 50px;
			width: 100%;
			z-index: 1;
		}
	}
}

// Cool underline-on-hover effect
.animatedUnderline(@colorDefault, @colorHover) {
	position: relative;
	
	&:after {
		display: block;
		position: absolute;
		content: ' ';
		top: 100%;
		left: 0;
		width: 15%;
		height: 4px;
		transition: width 0.2s, background-color 0.2s;
		background-color: @colorDefault;
	}

	&:hover:after {
		width: 40%;
		background-color: @colorHover;
	}
}

.flex(@direction: row, @align: center, @justify: flex-start, @wrap: wrap) {
	display: flex;
	flex-direction: @direction;
	align-items: @align;
	justify-content: @justify;
	flex-wrap: @wrap;
}

/* Focus et navigation alternative */
.text-link-focus{
	&:focus { outline: 0; text-decoration: underline; text-decoration-thickness: 2px; color: @link-focus-color}
	&:focus:not(:focus-visible) {
	/* Undo all the above focused & styles if the & has focus but the browser wouldn't normally show default focus styles */
	outline: 0; text-decoration: none;
	}
	&:focus-visible { outline: 0; text-decoration: underline;text-decoration-thickness: 2px;color: @link-focus-color }
}
.box-focus{
	&:focus { text-decoration:none; outline: 0; box-shadow: 0 0 0 2px @object-focus-color}
	&:focus:not(:focus-visible) {
	/* Undo all the above focused & styles if the & has focus but the browser wouldn't normally show default focus styles */
	outline: 0; box-sizing: none;
	}
	&:focus-visible {text-decoration:none ; outline: 0; box-shadow: 0 0 0 2px @object-focus-color }
}