@import (reference) "~@less/theme/vars.less";

.vjs-volume-panel {
  display: none;

  @media (min-width: @screen-xs-min) {
    display: flex;
    align-items: center;
    opacity: 0.6;
    .lh-transition(all .1s ease);
  }

  &.vjs-hover {
    width: 8.5em;
    opacity: 1;
    .lh-transition(all .1s ease);
  }

  .vjs-button {
    opacity: 1;
    transition: none;
  }

  .vjs-volume-control {
    min-height: @small-btn-height / @em;
    transition: none !important;
  }

  .vjs-mute-control {
    .vjs-icon-placeholder::before {
      content: "\e92b";
    }
  
    &.vjs-vol-0 {
      .vjs-icon-placeholder::before {
        content: "\e92a";
      }
    }
  }
}