@import (reference) "~@less/theme/vars.less";

.vjs-progress-control-container {
  width: 100%;
  margin-bottom: @small-space;
  margin-left: (@progress-bar-cursor-width / 2 + 12) / @em;
  margin-right: (@progress-bar-cursor-width / 2 + 12) / @em;
  
  @media (min-width: @screen-xxs-min) {
    margin-bottom: @large-space;
  }

  @media (min-width: @screen-xs-min) {
    margin-left: (@progress-bar-cursor-width / 2) / @em;
    margin-right: (@progress-bar-cursor-width / 2) / @em;
    font-size: 1.75em;
  }
}
  
.vjs-progress-control {
  width: 100%;

  .vjs-progress-holder {
    outline: none; // disable outline on touch screen
    margin: 0;
    height: @prograss-bar-height / @em;
  }

  .vjs-time-tooltip {
    font-size: 12px;
  }

  &:hover {
    .vjs-progress-holder {
      font-size: 1em;
      @media (hover: hover) and (min-width: @screen-xs-min) {
        font-size: 1.2em;
        height: @prograss-bar-height * 1.75 / @em;
      }
    }
  }

  @media (hover: none) {
    .vjs-slider:focus {
      text-shadow: none;
      box-shadow: none;
    }
  }

  .vjs-play-progress::before {
    outline: none; // disable outline on touch screen
    font-size: @progress-bar-cursor-width / @em;
    top: 50%;
    transform: translateY(-50%);
    color: var(--accent-color);
    @media (min-width: @screen-xs-min) {
      font-size: 1em;
    }
  }
}

.vjs-progress-holder .vjs-play-progress {
  background: var(--accent-color);
}