@import (reference) "~@less/shared/utilities.less";

:root {
  --accent-color: transparent;
}

@colors: red orange yellow green turquoise blue violet pink;
// loop definition
.accentcolors(@list, @i: 1) when (@i <= length(@list)) {
  // extract the right color from the list
  @color: extract(@list, @i);
  @colorVar: "nfb-@{color}";

  // apply the background to the selector
  .accent-@{color} {
    --accent-color: @@colorVar;
  }

  // recursive call for the next color
  .accentcolors(@list, @i + 1);
}
.accentcolors(@colors);
.accent-purple {
  --accent-color: @nfb-violet;
}