@import (reference) "~@less/shared/utilities.less";
@import (reference) "~@less/theme/vars.less";

// Play/pause 
.vjs-big-play-button, .vjs-play-control {
  opacity: 1;
  font-size: 20 / @em; // 20px
  width: @small-btn-height / 20 * 1em; // 40px (base 20)
  height: @small-btn-height / 20 * 1em; // 40px (base 20)
  min-height: 0;
  background: var(--accent-color);
  border: 2px solid var(--accent-color);
  border-radius: 50%;
  .lh-transition(all .2s linear);

  @media (min-width: @screen-sm-min) {
    font-size: 2.375em; // 38px (height: 76px)
    border-width: 4px;
  }

  .vjs-icon-placeholder::before {
    content: "\e90c";
    font-family: 'balmoral-icons';
    font-size: 1em;
    padding-left: 4px;
    color: @white;
    position: relative;
    display: block;
    .lh-transition(color .2s linear);
  }

  &:hover {
    background: @white;
    .vjs-icon-placeholder::before {
      color: var(--accent-color);
      .lh-transition(color .2s linear);
    }
  }

  &:focus:focus-visible {
    outline: none;
    box-shadow: 0 0 0 3px @black, 0 0 0 6px @grey-100;
  }
}

.vjs-big-play-button {
  top: auto;
  bottom: @small-space;
  left: @small-space;
  display: flex;
  align-items: center;
  justify-content: center;
  @media (min-width: @screen-xs-min) {
    bottom: @large-space;
    left: @large-space;
  }

  .vjs-has-started& {
    display: none;
  }

  :hover& {
    background: @white;
    .vjs-icon-placeholder::before {
      color: var(--accent-color);
      .lh-transition(color .2s linear);
    }
  }
}

.vjs-play-control.vjs-playing {
  .vjs-icon-placeholder::before {
    content: "\e959";
    padding-left: 0;
  }
}