@import (reference) "~@less/shared/utilities.less";
@import (reference) "~@less/theme/vars.less";

.vjs-control-bar {
  flex-wrap: wrap;
  height: auto; 
  background: none;
  padding: @small-space;
  align-items: center;
  justify-content: space-between;
  .smooth-gradient();
  z-index: 2;
  // font-size: 0.83333em;

  @media (min-width: @screen-xs-min) {
    padding: @large-space;
    font-size: 1em;
  }

  .vjs-play-progress .vjs-time-tooltip {
    display: none;
  }
}

.vjs-control-custom-container {
  display: flex;
  z-index: 1;
}