@import "~@less/shared/fonts.less";
@import (reference) "~@less/shared/utilities.less";
@import (reference) "~@less/theme/vars.less";

@import "~@less/theme/modules/accent-color.less";

.video-js.vjs-nfb-theme-default {
  font-size: @font-size*1px;
  font-family: @font-family-sans-serif;
  overflow: hidden;

  // General
  @import "~@less/theme/modules/control-bar.less";
  @import "~@less/theme/modules/control-button.less";
  @import "~@less/theme/modules/menu.less";

  .vjs-modal-dialog {
    z-index: 1;
  }

  // Fix background: none on focus
  select:focus {
    background: @white;
  }
  vjs-menu li.vjs-selected, .vjs-menu li.vjs-selected:focus {
    background: @white;
  }
  // end fix

  // Specific
  @import "~@less/theme/modules/play-pause.less";
  @import "~@less/theme/modules/progress-control.less";
  @import "~@less/theme/modules/volume.less";
  @import "~@less/theme/modules/duration.less";
  @import "~@less/theme/modules/text-track-settings.less";

  // Poster
  .vjs-poster {
    background-size: cover;
  }
  
  // Seek Buttons
  .vjs-seek-button {
    &.vjs-forward {
      .vjs-icon-placeholder::before {
        content: "\e987";
        transform: scaleX(-1);
      }
    }

    &.vjs-backward {
      .vjs-icon-placeholder::before {
        content: "\e987";
      }
    }
  }

  // Spacer
  .vjs-spacer {
    display: block;
    @media (min-width: @screen-xs-min) {
      flex-grow: 1;
    }
  }

  // CC
  .vjs-subs-caps-button {
    .vjs-icon-placeholder::before {
      content: "\e958";

      .vjs-language-fr& {
        content: "\e956";
      }
    }

    .vjs-menu-item {
      text-transform: none;
    }
  }

  // DV
  .vjs-audio-description-button {
    cursor: pointer;
    .vjs-icon-placeholder::before {
      content: "\e903";
    }

    &.dv-playing {
      color: var(--accent-color);
    }
  }

  // Quality selector
  .vjs-quality-selector {
    display: none;
    @media (min-width: @screen-xs-min) {
      display: inherit;
    }

    .vjs-menu {
      width: 5em;
    }
    .vjs-menu-button {
      font-size: @fs-xsmall;
    }

    // for mp4
    .vjs-mp4-source& {
      .vjs-icon-placeholder::before {
        content: "\e943";
        font-size: 1em;
      }
    }
  }

  // Fullscreen
  .vjs-fullscreen-control {
    .vjs-icon-placeholder::before {
      content: "\e92c";

      .vjs-fullscreen& {
        content: "\e92d";
      }
    }
  }

  // Chromecast
  .vjs-chromecast-button {
    .vjs-icon-placeholder {
      background: none;
      display: block;
      width: auto;
      height: auto;

      &::before {
        content: "\e97b";
      }
    }

    &.vjs-chromecast-casting-state {
      color: var(--accent-color);

      .vjs-icon-placeholder::before {
        content: "\e981";
      }
    }
  }

  // AirPlay
  .vjs-airplay-button {
    .vjs-icon-placeholder {
      background: none;
      display: block;
      width: auto;
      height: auto;

      &::before {
        content: "\e97a";
      }
    }
  }
}