.vjs-text-track-settings {
  z-index: 2;
  background: @black !important;
  background: linear-gradient(to bottom,rgba(0,0,0,0.8) 0%,rgba(62,62,62,0.8) 100%) !important;
  backdrop-filter: blur(8px);
  height: 100%;
  
  .vjs-modal-dialog-content {
    font-size: @fs-small;
    padding: @small-space;
    
    .vjs-text-opacity, .vjs-bg-opacity, .vjs-edge-style, .vjs-font-family, .vjs-window-color {
      display: none;
    }
  }

  .vjs-track-settings-colors {
    align-self: center;
    justify-self: right;
    margin-right: 2em;
  }

  .vjs-track-settings-font {
    align-self: center;
    margin-left: 2em;
  }

  .vjs-close-button {
    width: auto;
    align-items: flex-start;
    top: @small-space;
    right: @small-space;
    opacity: 1;

    .vjs-icon-placeholder::before {
      content: "\e950";
    }
  }

  fieldset {
    margin: 0;
    padding: 0;
  }

  @media (min-width: @screen-sm-min) {
    .vjs-modal-dialog-content {
      font-size: 1em;
      padding: @large-space;
      
      .vjs-text-opacity, .vjs-bg-opacity {
        display: inline-block;
      }

      .vjs-edge-style, .vjs-font-family, .vjs-window-color {
        display: inherit;
      }
    }

    .vjs-close-button {
      top: @large-space;
      right: @large-space;
    }
  }
}